<template>
	<div class="app-container" v-loading="isLoad" >
		<el-form :label-position="labelPosition" label-width="80px" :model="form" ref='form' style="width:500px ;margin:0 auto;" :rules="rules">
			<el-form-item label="选择角色:">
				<el-select v-model="form.id" filterable placeholder="请选择" @change="companyChange" style="width: 100%;" :disabled="isdisabled">
					<el-option v-for="item in itemList" :key="item.mid?item.mid:item.ctId" :label="item.mname?item.mname:item.ctName" :value="item.mid?item.mid:item.ctId">
					</el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="公司名称:">
				<el-input v-model="form.company" placeholder="请输入公司名称" disabled></el-input>
			</el-form-item>
			<el-form-item label="收款银行账号:" prop="bankCardAccount">
				<el-input type="bankCardAccount" v-model.number="form.bankCardAccount" placeholder="请输入收款银行账号" :disabled="isdisabled"></el-input>
			</el-form-item>
			<el-form-item label="收款银行账号名:" prop="bankCardName" v-if="false">
				<el-input type="bankCardAccount" v-model.number="form.bankCardAccount" placeholder="请输入收款银行账号"></el-input>
			</el-form-item>
			<el-form-item label="银行账号开户行:" prop="bankName">
				<el-input v-model="form.bankName" placeholder="请输入银行名字" :disabled="isdisabled"></el-input>
			</el-form-item>
			<el-form-item>
				<el-button @click="saveAction" :disabled="isdisabled">提交</el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
/* eslint-disable quotes,indent,keyword-spacing,spaced-comment,no-var */
import {
	getListManufacturerGeneral,
	giveManufacturerAuthority
} from '../../../api/admin/usual';
import {
	getManufacturerInfoByLogin,
	saveBankCardInfo
} from '../../../api/admin/basicData';
import { backCN } from 'utils/index'
export default {
	data() {
		return {
			labelPosition: 'left',
			itemList: [],
			form: {
				id: '',
				company: '',
				bankCardAccount: '',
				bankCardName: '',
				bankName: '',
			},
			rules: {
				bankCardAccount: [{ required: true, message: '收款银行账号不能为空' }
					, { type: 'number', message: '收款银行账号必须为数字值', trigger: 'blur' }],
				bankName: [{ required: true, message: '银行账号开户行不能为空' }],
			},
			cur_mid: 0,
			isLoad: true,
			isdisabled: false
		}
	},
	watch: {},
	mounted() {
		this.listManufacturerGeneral();
	},
	methods: {
		saveAction() {
			this.$refs['form'].validate((valid) => {
				if (valid) {
					let account = this.form.bankCardAccount
					let name = this.form.bankCardName
					let bank = this.form.bankName
					this.isLoad = true;
					saveBankCardInfo({ account, name, bank }).then(res => {
						this.isLoad = false
						this.isdisabled = true
						res.msg === 0 ? this.$message.success("保存成功！") : this.$message.error(backCN(res.code))

					})
				} else {
					return false;
				}
			});
		},
		companyChange(val) {
			this.manufacturerInfoByLogin(val);
		},
		manufacturerInfoByLogin(mid) {
			this.isLoad = true
			giveManufacturerAuthority({
				mid: mid
			})
				.then(res => {
					this.isLoad = false
					if (res.msg == 0) {
						this.isLoad = true
						getManufacturerInfoByLogin()
							.then(res => {
								this.isLoad = false
								console.log("res", res)
								if (res.msg == 0) {
									this.form.company = res.name;
									this.form.bankCardAccount = res.bankCardAccount ? Number(res.bankCardAccount) : '';
									this.form.bankCardName = res.bankCardName;
									this.form.bankName = res.bankName;
								}
							});
					}
				});
		},
		listManufacturerGeneral() {
			getListManufacturerGeneral()
				.then(res => {
					if (res.msg == 0) {
						this.itemList = res.mList;
						this.form.id = res.mList[0].mid;
						this.cur_mid = res.mList[0].mid;
						this.manufacturerInfoByLogin(this.cur_mid);
					}
				});
		},
	}
};
</script>

<style scoped>
.app-container {
	background: #f5f5f5;
}

.item-left {
	padding: 7px 0;
}
</style>


