import fetch from 'utils/fetch';

//获取匹配的iccid
export function getListIotCardByMatch(data) {
	// 参数     matchValue=11
	return fetch({
		url: '/recharge/manager/listIotCardByMatch.do',
		method: 'get',
		data: data
	});
}

//获取厂商
export function getListManufacturerGeneral() {
	return fetch({
		url: '/recharge/manager/listManufacturerGeneral.do',
		method: 'get'
	});

}

//获取电信角色
export function getListCTInfoGeneral(data) {
	return fetch({
		url: '/recharge/manager/listCTInfoGeneral.do',
		method: 'get',
		data: data
	});
}

//管理员授予厂商角色
export function giveManufacturerAuthority(data) {
	//参数  mid=1
	return fetch({
		url: '/recharge/manager/giveManufacturerAuthority.do',
		method: 'post',
		data: data
	});

}
//赋予admin电信权限
export function giveCTAuthority(data) {
	// 参数  ctId=1
	  return fetch({
	    url: '/recharge/manager/giveCTAuthority.do',
	    method: 'post',
	    data: data
	  });
}
//联合行为，获取厂商信息加上发送mid到服务器
export function getAndGive() {
	return new Promise((resolve, reject) => {
		getListManufacturerGeneral()
			.then(res => {
				if(res.msg == 0) {
					giveManufacturerAuthority({
							mid: res.mList[0].mid
						})
						.then(res2 => {
							if(res2.msg == 0) {
								resolve(res)
								//this.ListIotCardInfo(1);
							} else {
								reject("改变mid发生网络错误");
								//								this.$message.error('改变mid发生网络错误');
							}
						});
				} else {
					reject("获取厂商列表信息发生网络错误");
				}
			})
	})
}


