
/*******基本资料*********/
import fetch from 'utils/fetch';


//获取厂商信息
export function getManufacturerInfoByLogin(data) {
	// 参数   

	  return fetch({
	    url: '/recharge/manager/getManufacturerInfoByLogin.do',
	    method: 'get',
	    data: data
	  });
}
//修改厂商信息
export function updateManufacturerInfo(data){
	return fetch({
		url:'/recharge/manager/updateManufacturerInfo.do',
		method:'post',
		data:data
	})
}


//获取电信信息
export function getCTInfo() {
	// 参数
	  return fetch({
	    url: '/recharge/manager/getCTInfo.do',
	    method: 'get'
	  });
}
export function updateCTInfo(data){
	return fetch({
		url:'/recharge/manager/updateCTInfo.do',
		method:'post',
		data:data
	})
}
//根据服务器session获取流量包详情
export function getListManufacturerPackage() {
	// 参数
	  return fetch({
	    url: '/recharge/manager/listManufacturerPackage.do',
	    method: 'get'
	  });
}

//修改银行信息
export function saveBankCardInfo(data){
	return fetch({
		url:'/recharge/manager/saveBankCardInfo.do',
		method:'post',
		data:data
	})
}
//修改自管理门户信息
export function updateManufacturerKey(data){
	return fetch({
		url:'recharge/manager/updateManufacturerKey.do',
		method:'post',
		data:data
	})
}
//获取基本资料中的分成信息
export function listManufacturerRatio(){
	return fetch({
		url:'recharge/manager/listManufacturerRatio.do',
		method:'get',
	})
}
//添加或修改厂商分成信息
export function setManufacturerRatio(data){
	return fetch({
		url:'recharge/manager/setManufacturerRatio.do',
		method:'post',
		data:data
	})
}
//获取电信分成信息
export function getCTRatio(){
	return fetch({
		url:'recharge/manager/getCTRatio.do',
		method:'get'
	})
}
//添加或修改电信分成信息
export function setCTRatio(data){
	return fetch({
		url:'recharge/manager/setCTRatio.do',
		method:'post',
		data:data
	})
}


